import { Router } from "express";
import swaggerUi from "swagger-ui-express";
import swaggerDocsPaths from "./swagger-docs-paths.json";

export const swagger = Router();

export const swaggerDoc = {
  openapi: "3.0.1",
  info: {
    title: "Swagger",
    version: "1.0.0",
  },
  components: {
    securitySchemes: {
      bearerAuth: {
        type: "http",
        scheme: "bearer",
        bearerFormat: "JWT",
      },
    },
  },
  security: [
    {
      bearerAuth: [],
    },
  ],
  paths: swaggerDocsPaths,
};

if (process.env.NODE_ENV === "production") {
  swagger.use("/api-docs", (req, res) => {
    return res.status(404).send({ error: "not found" });
  });
}

swagger.use("/api-docs", swaggerUi.serve, swaggerUi.setup(swaggerDoc));
swagger.get("/", (req, res) => res.redirect("/api-docs"));
